html {}
body {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  box-sizing: border-box;
  position: relative; }
.container {
  width: 100% !important;
  max-width: 1250px;
  box-sizing: border-box;
  @include r(1200) {
    padding: 0 15px; } }
a {
  text-decoration: none;
  transition: all 0.3s; }
input[type="submit"] {
  appearance: none;
  -webkit-appearance: none; }
textarea {
  resize: none; }


@include r(767) {
	.hidden-xs {
		display: none !important; } }
@media only screen and (min-width: 768px) and (max-width: 1024px) {
	.hidden-sm {
		display: none !important; } }
@include rmin(1024) {
	.hidden-md {
		display: none !important; } }


.btn {
  border-radius: 25px;
  padding: 13px 36px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: bold;
  line-height: 1.79;
  @include center;
  display: inline-flex;
  i {
    margin-left: 10px;
    position: relative;
    top: -1px; }
  &-primary {
    background-color: $dark-two;
    color: $white;
    border: 1px solid $dark-two;
    &:hover {
      background: none;
      color: $dark-two; } }
  &-secondary {
    background: $white;
    color: $dark-two;
    border: 1px solid #ffffff;
    &:hover {
      background: none;
      color: #ffffff; } } }
